import React from 'react';
import { Chip } from '@mui/material';

export const AvailabilityChip = ({ availableSpots }) => {
	const color = availableSpots ? 'success' : 'default';
	const label = availableSpots ? ` ${availableSpots} ` : '  ';

	return (
		<Chip
			size='small'
			label={label}
			color={color}
		/>
	);
};
